import React from 'react'
import { connect } from 'react-redux';
import * as actions from '../../actions';
import { useHistory } from 'react-router-dom';

function Navigate(props){
  let history = useHistory();

    let doLogout = ()=>{
      props.logout();
      history.push('/');
    }

    return (
        <nav class="green darken-4">
    <div class="nav-wrapper container">
      <a href="#" class="brand-logo">Todo</a>
      {props.mode === 'home' ? <div></div> : <ul id="nav-mobile" class="right hide-on-med-and-down">
        <li><a href="badges.html">Name</a></li>
        <li><a onClick={()=>doLogout()}>Logout</a></li>
      </ul>}
    </div>
  </nav>
        )
        
   
}


export default connect(null, actions)(Navigate);
